.clockWrapper{position:relative;display:flex;justify-content:center;align-items:center;border-radius:50%;background:url("./циферблат.png") no-repeat center center/93% 93%,url("./фон-полоски.png") center center/50%;border:4px solid;width:55vh;height:55vh;box-shadow:0 -15px 15px rgba(255,255,255,.05),inset 0 -15px 15px rgba(255,255,255,.05),0 15px 15px rgba(0,0,0,.3),inset 0 15px 15px rgba(0,0,0,.3)}.clockWrapper:before{content:"";position:absolute;width:10px;height:10px;border-radius:50%;background:#000;z-index:111}.clockWrapper .hour{position:absolute}.clockWrapper .hour .hours{width:160px;height:200px;display:flex;justify-content:center;position:relative;border-radius:50%;transition:1s linear}.clockWrapper .hour .hours:before{content:"";position:absolute;width:1.1vh;height:13vh;z-index:10;border-radius:6px 6px 0 0;background:red;background-size:100px;top:50%;left:50%;transform:translate(-50%, -100%)}.clockWrapper .minute{position:absolute}.clockWrapper .minute .minutes{width:160px;height:270px;display:flex;justify-content:center;position:relative;border-radius:50%;transition:1s linear}.clockWrapper .minute .minutes:before{content:"";position:absolute;width:.9vh;height:18vh;z-index:10;border-radius:6px 6px 0 0;background:#000;background-size:100px;top:50%;left:50%;transform:translate(-50%, -100%)}.clockWrapper .second{position:absolute}.clockWrapper .second .seconds{width:160px;height:300px;display:flex;justify-content:center;position:relative;border-radius:50%;transition:1s linear}.clockWrapper .second .seconds:before{content:"";position:absolute;width:.3vh;height:24vh;z-index:10;border-radius:6px 6px 0 0;background:#000;background-size:100px;top:50%;left:50%;transform:translate(-50%, -85%)}.buttonStyle{display:flex;justify-content:center;align-items:center;cursor:pointer;width:75px;height:75px;border-radius:50%;text-align:center;border:10px #000;background:rgba(241,255,197,.7);margin-top:20px}